export function universalFunc(numberOfPlane: string, inputValue: string, channel: string, indexOfTable: number = 0, key: string) {

    if (inputValue === '') {
        return true
    }
    debugger
    let value = 0
    if (key === '14') value = loadChecksState(numberOfPlane).head1[0].valuesOfBlocks[0][channel]
    if (key === '15') value = loadChecksState(numberOfPlane).head1[0].valuesOfBlocks[1][channel]
    if (key === '13') value = loadChecksState(numberOfPlane).head6[0].valuesOfBlocks[indexOfTable - 2][channel]
    if (key === '25') value = loadChecksState(numberOfPlane).head12[0].valuesOfBlocks[indexOfTable - 2][channel]

    // @ts-ignore.
    return compareFunc.call(this, numberOfPlane, (+inputValue*1000 - value*1000)/1000, channel, indexOfTable)
}

export const numToFunc = {
    0: emptyFunc,
    1: compareFunc,
    2: dividerFunc,
    3: universalFunc,
    4: subtractionFunc
}


